//import { UserOutlined } from "@ant-design/icons";
import { Menu } from "antd";
import { ReactElement, useContext } from "react";
import { Link } from "react-router-dom";
import logo from "./WATlogo.jpg";
//import { LinkItem } from "../model/LinkItem";

import "./Topbar.css";

const { SubMenu } = Menu;

interface LinkItem {
  name: string;
  href: string;
  exact: boolean;
  icon: ReactElement;
  component?: ReactElement;
  requiresLogin?: boolean;
  subItems?: LinkItem[];
}

interface TopbarProps {
  links: LinkItem[];
}


const Topbar = ({ links}: TopbarProps) => {


  return (
    <Menu mode="horizontal" id="top-bar">
      <Menu.Item key="logo">
      <Link to={links[0].href}>
          <img src={logo} width="30" height="30" alt="Logo" />

        </Link>
      </Menu.Item>
      {links
        .map((link) =>
          link.subItems ? (
            <SubMenu key={link.href} title={link.name} icon={link.icon}>
              {link.subItems.map((s) => (
                <Menu.Item key={s.href} icon={s.icon}>
                  <Link to={s.href}>{s.name}</Link>
                </Menu.Item>
              ))}
            </SubMenu>
          ) : (
            <Menu.Item key={link.href} icon={link.icon}>
              <Link to={link.href} className="text-white align-center">
                {link.name}
              </Link>
            </Menu.Item>
          )
        )}



    </Menu>
  );
};

export default Topbar;
