// import { CompassOutlined, LoadingOutlined } from "@ant-design/icons";
import { Col, Row } from "antd";
// import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import background from "./background.jpg";

import "./HomePage.css";



const Home = () => {

  

  return (
    <div
      className="homepage-banner"
      style={{ backgroundImage: "url(" + background + ")" }}
    >
      <h1 className="page-title main-title">WCA Archive</h1>

      <Row gutter={16} justify="center">
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>World Championship Articles</h3>
            <p>
              Check out our{" "}
              <Link to="wc-list">list of interesting articles about past world championships.</Link>
            </p>


          </div>
        </Col>
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>Event Articles</h3>
            <p>
              Check out our{" "}
              <Link to="event-list">list of articles on the history of WCA events.</Link>
            </p>
          </div>
        </Col>
        <Col xs={20} md={6}>
          <div className="stat-card">
            <h3>Other Articles</h3>
            Check out our{" "}
              <Link to="event-list">list other articles on the history of the WCA.</Link>

            
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Home;
